 */
header {
  position: fixed;
  padding: 0.5rem 0;
  min-height: 95px;
  z-index: 40;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: Georgia, "Times New Roman", Times, serif;
  color: #ebd693;
  background-color: #2a2a2a;
  /* background: \$gradient-background; */
  border-bottom: 2px solid #ebd693;
}

a {
  font-size: 1rem;
  background-color: none;
  margin: 0 1.5rem;
  cursor: pointer;
}

#home-tab {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 5rem;
}
#home-tab #home-heading {
  font-family: "Rozha One", serif;
  color: #a3771f;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 1.8rem;
  text-shadow: 0.8px 0.8px #17cfc6;
  letter-spacing: 1.8px;
}
#home-tab #home-sub-heading {
  font-size: 1rem;
  color: white;
  margin-top: -0.15rem;
}

#right-side {
  padding-right: 2rem;
}

#right-side a {
  text-transform: uppercase;
}

#right-side a.active {
  border-bottom: 4px solid #17cfc6;
  transition: border-width 0.2s ease-in-out;
}

#menu-button {
  display: none;
}

#dropdown-box {
  position: absolute;
  top: 0;
  z-index: -5;
}

#dropdown-box.active {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  position: absolute;
  z-index: 1;
  min-width: 100vw;
  top: 95px;
  padding: 0.5rem 0;
  gap: 0.9rem;
  background-color: #2a2a2a;
  border: 1px solid #ebd693;
  border-top: none;
  border-right: none;
  border-left: none;
  transition: background-color 0.2s, top 0.2s, z-index 0.2s;
}
#dropdown-box.active a {
  color: #ebd693;
  font-size: 1.5rem;
  text-transform: uppercase;
}

@media only screen and (max-width: 600px) {
  #right-side {
    display: none;
  }
  #menu-button {
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    margin-right: 2.5rem;
    width: max-content;
    height: max-content;
    background-color: #2a2a2a;
    border: none;
    cursor: pointer;
  }
  #menu-button .line {
    width: 3rem;
    height: 0.3rem;
    border-radius: 100px;
    background-color: #ebd693;
    transform-origin: left center;
    transition: rotate 0.5s, width 0.5s, translate 0.5s, opacity 0.5s, background-color 0.5s;
  }
  #menu-button.active #line2 {
    width: 0;
    opacity: 0;
  }
  #menu-button.active #line1, #menu-button.active #line3 {
    background-color: #17cfc6;
  }
  #menu-button.active #line1 {
    translate: 0 -0.15rem;
    width: 2.4038rem;
    rotate: 45deg;
  }
  #menu-button.active #line3 {
    translate: 0 0.15rem;
    width: 2.4038rem;
    rotate: -45deg;
  }
  #home-tab {
    margin-left: 3rem;
  }
